body {
  background-color: #87CEFA; 
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  display: flex;
  flex-direction: column;
  justify-content: center; /* Center content vertically */
  align-items: center; /* Center content horizontally */
  min-height: 100vh; /* Set a minimum height of the viewport height */
  background-color: #87CEFA; /* Move the background color here from body */
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center; /* Center content vertically */
  align-items: center; /* Center content horizontally */
  height: 80vh; /* Utilize 80% of the viewport height for container */
}

.ready-btn {
  padding: 15px 30px;
  font-size: 24px;
  margin-top: 20px;
  background-color: #602f6b;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.ready-btn:hover {
  background-color: #794185;
}

.practice {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 350px; /* Reduce the width to make it more compact */
  background-color: #ADD8E6;
  border-radius: 8px;
}

.problem-display {
  display: flex;
  flex-direction: row;
  align-items: center; /* Align the items vertically */
  font-size: 24px; /* Decrease font size */
  font-weight: bold;
  color: #602f6b;
  padding: 10px;
  margin: 10px;
  background: linear-gradient(to left, #8bc7d6, #9FC3D6);
  border-radius: 6px;
}

.message-display {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; /* Distribute space between messages */
  font-size: 24px; /* Decrease font size */
  color: #602f6b;
  padding: 3px 5px;
  margin: 5px;
  min-height: 32px;
}

input[type="number"] {
  width: 50px;
  border: none;
  outline: none;
  font-size: 26px;
  font-weight: bold;
  color: #602f6b;
  background-color: transparent; /* Make the input field transparent */
  text-align: center;
}

/* Hide spin arrows in webkit-based browsers */
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}


